.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  button {
    background-color: #2d2d2d;
    box-shadow: 0 4px 18px rgba(33, 38, 143, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);
    border-radius: 30px;
    margin: 5px;
    color: white;
    border: none;
    padding: 8px 28px;
    cursor: pointer;
    font-size: 16px;
    font-family: Roboto;
  }
  button:hover {
    background: #0b7dda;
  }
  button:disabled {
    color: rgba(255, 255, 255, .3);
  }
}
